.header {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    background-color: $grey;
    width: 100%;
    height: 100%;
    border-radius: $borderRadius;
    margin: 5px;
    padding: 50px;

    h2 {
        font-size: 3rem;
        background-color: $grey;
    }
    h3 {
        font-size: 2rem;
        background-color: $grey;
    }

    .info {
        font-size: 1.3rem;

        p {
            background-color: $grey;
        }
    }
}
@media screen and (max-width: 700px) {
    .header {
        flex-direction: column;
    }
}