.navigation {
    width: 100%;

    ul {
        display: flex;
        width: 100%;
        align-items: center;
        justify-content: space-evenly;

        li {
            display: flex;
            align-items: center;
            justify-content: center;
            background-color: $grey;
            border-radius: $borderRadius;
            width: 100%;
            height: 50px;
            margin: 5px;
            cursor: pointer;
            font-size: 1.3rem;
        }
    }
}