@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro&display=swap);
*{background-color:#aaa;box-sizing:border-box;margin:0;padding:0}body{margin:1rem auto;max-width:1200px;font-family:"Source Sans Pro",sans-serif;color:#ddd}li{list-style-type:none}.content{display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap}.header{display:-webkit-flex;display:flex;-webkit-justify-content:space-evenly;justify-content:space-evenly;-webkit-align-items:center;align-items:center;background-color:#333;width:100%;height:100%;border-radius:25px;margin:5px;padding:50px}.header h2{font-size:3rem;background-color:#333}.header h3{font-size:2rem;background-color:#333}.header .info{font-size:1.3rem}.header .info p{background-color:#333}@media screen and (max-width: 700px){.header{-webkit-flex-direction:column;flex-direction:column}}.navigation{width:100%}.navigation ul{display:-webkit-flex;display:flex;width:100%;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-evenly;justify-content:space-evenly}.navigation ul li{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;background-color:#333;border-radius:25px;width:100%;height:50px;margin:5px;cursor:pointer;font-size:1.3rem}.experiences,.competences,.formations{height:100%;width:100%;margin:5px;background-color:#333;display:-webkit-flex;display:flex;-webkit-justify-content:space-evenly;justify-content:space-evenly;border-radius:25px}.experiences ul,.competences ul,.formations ul{margin:5vh;font-size:1.3rem;background-color:#333}.experiences li,.competences li,.formations li{margin:2vh;font-size:1rem;background-color:#333}.experiences li p,.competences li p,.formations li p{background-color:#333}

