.experiences, .competences, .formations {
    height: 100%;
    width: 100%;
    margin: 5px;
    background-color: $grey;
    display: flex;
    justify-content: space-evenly;
    border-radius: $borderRadius;
    ul{
        margin: 5vh;
        font-size: 1.3rem;
        background-color: $grey;
    }
    li {
        margin: 2vh;
        font-size: 1rem;
        background-color: $grey;

        p {
            background-color: $grey;
        }
    }
}