@import url('https://fonts.googleapis.com/css2?family=Source+Sans+Pro&display=swap');

$font: 'Source Sans Pro', sans-serif;

$grey: #333;
$white: #ddd;

$borderRadius: 25px;


* {
    background-color: #aaa;
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}
body{
    margin: 1rem auto;
    max-width: 1200px;
    font-family: $font;
    color: $white;
}
li {
    list-style-type: none;
}
